import React, { useEffect } from "react";

import { createRoot } from "react-dom/client";
import { Helmet } from "react-helmet";

import { createGlobalStyle } from "styled-components";


import NavBar from "./components/navbar";
import { Title } from "./components/title";

import GlArticle from "./components/glArticle";
import TwoArticle from "./components/twoArticle";

import { customScrollTo, easeOutSine } from "./components/customScroll";

// import { motion, useScroll, useSpring, useTransform } from 'framer-motion';


// const { scrollYProgress } = useScroll();
// const smoothProgress = useSpring(scrollYProgress, { mass: 0.1 });


const GlobalStyle = createGlobalStyle`
	html {
		font-family: 'Inter', sans-serif;
		letter-spacing: 2.5px;
	}

	body {
		width: 100%;
		overflow-x: hidden;
	}
`;

export default function App() {
	console.log("render App");

	useEffect(() => {

		let dy = 0;
		window.addEventListener("wheel", async e => {
			// Prevent the default way to scroll the page
			e.preventDefault();

			dy += e.deltaY;
			const _dy = dy; // Store the value of "dy"
			// await delay(50); // Wait for .15s

			// If the value hasn't changed during the delay, then scroll to "start + dy"
			if (_dy === dy) {
				const start = window.scrollY || window.scrollY;
				customScrollTo(start + dy, 250, easeOutSine);
				dy = 0;
			}
		}, { passive: false });
	}, []);

	//TODO: Smooth scrolling

	return (
		<>
			<Helmet>
				<title>hxnzen - Home</title>
				<meta name="title" content="kxnzen - Home" />
				<meta name="description" content="A portfolio site for my work." />

				<meta property="og:type" content="website" />
				<meta property="og:url" content="https://kxnzen.github.io" />
			</Helmet>

			<GlobalStyle />
			<NavBar />
			<Title title="Welcome" />
			<GlArticle />
			<TwoArticle />
		</>
	);
}

const root: Element = document.body;
createRoot(root).render(<App />);