/* eslint-disable @typescript-eslint/no-explicit-any */
import {
    DirectionalLight,
    EdgesGeometry,
    LineBasicMaterial,
    LineSegments,
    Mesh,
    MeshStandardMaterial,
    PerspectiveCamera,
    Scene,
    WebGLRenderer
} from 'three';

// @ts-expect-error NOT TYPED
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
// @ts-expect-error NOT TYPED
import { OrbitControls } from 'three/addons/controls/OrbitControls.js';


let camera: PerspectiveCamera, scene: Scene, renderer: WebGLRenderer, controls: OrbitControls;
let elem: HTMLElement = null, canvas: HTMLCanvasElement = null, canvasParent: HTMLElement = null;

/**
 * Creates the Threejs scene and places it in the element provided by param
 */
export default class Three {

    constructor(element: HTMLElement) {
        elem = element;
        this.init();
    }
    init(): void {
        window.addEventListener('resize', onElementResize, false);

        try {
            scene = new Scene();

            renderer = new WebGLRenderer({
                antialias: false,
                canvas: elem,
                precision: 'lowp',
                powerPreference: 'low-power',
                alpha: true
            });

            // Move ThreeJS camera with window scroll
            window.addEventListener('scroll', function () {
                // Get the scroll position
                const scrollPosition = window.scrollY;

                // Map the scroll position to a suitable range for the camera's y position
                // This mapping can be adjusted based on your specific needs
                const cameraY = scrollPosition * 0.0035;

                // Set the y position of the camera
                camera.position.y = cameraY;
            });

            canvas = renderer.domElement;
            canvasParent = canvas.parentElement;

            renderer.setPixelRatio(window.devicePixelRatio);
            renderer.setSize(elem.offsetWidth, elem.offsetHeight);
            renderer.shadowMap.enabled = true;
            renderer.shadowMap.type = 1;

            camera = new PerspectiveCamera(35, elem.offsetWidth / elem.offsetHeight, 0.1, 10);

            controls = new OrbitControls(camera, elem);

            controls.enableDamping = true;

            controls.enableZoom = false; // Disable Zoom
            controls.enablePan = false; // Disable Panning
            controls.keys = {}; // Disable KB controls

            controls.zoomSpeed = 0.5;
            controls.rotateSpeed = 0.125;
            controls.dampingFactor = 0.02; // lower = softer movement

            controls.maxPolarAngle = 1.62; // How far Down can you look
            controls.minPolarAngle = 1; // How far Up can you look

            controls.maxDistance = 6.5;
            controls.minDistance = 6.5;


            // controls.maxAzimuthAngle = [0.5, 0.6];

            // MODEL LOADING
            const loader: GLTFLoader = new GLTFLoader();

            loader.load('./test.glb',
                // On Load
                function (gltf: GLTFLoader) {
                    gltf.scene.traverse(function (node: any) {
                        if (node instanceof Mesh) {
                            node.castShadow = true;
                            node.receiveShadow = true;
                            // node.material.wireframe = true;
                            node.material.roughness = 0.2;


                            if (node.material instanceof MeshStandardMaterial) {
                                node.material.metalness = 1;
                            }

                            // Create an edges geometry from the mesh
                            const edges = new EdgesGeometry(node.geometry, 16);
                            const lineMat = new LineBasicMaterial(
                                { color: 0x000000, linewidth: 10, linecap: 'round', linejoin: 'round' });
                            // Create a line segments object to show the edges
                            const line = new LineSegments(edges, lineMat);
                            line.position.z -= 0.135; // Adjust this value as needed
                            line.position.y -= 0.15; // Adjust this value as needed
                            // Add the line segments to the scene
                            // Add the line segments to the scene
                            scene.add(line);
                        }
                    });
                    scene.add(gltf.scene);
                },
                // On Progress
                undefined,
                // On Error
                function (error: GLTFLoader) {

                    console.error(error);

                });


            const light = new DirectionalLight(0xffffff, 0.5,);
            light.castShadow = true;
            light.position.set(5, 20, 50);
            scene.add(light);

            // Start position

            camera.rotation.set(0, 0, 0);

            camera.position.set(35, 0, 10);

            update();
        }
        catch (err) {
            console.error(err);
        }
    }
}

function onElementResize() {
    camera.updateProjectionMatrix();
    controls.update();


    renderer.setSize(canvasParent.clientWidth, canvasParent.clientHeight, false);

    console.log(canvasParent.clientWidth, canvasParent.clientHeight);


}

function update() {
    requestAnimationFrame(update);
    renderer.render(scene, camera);
    controls.update();
}