import React from "react";

import styled from "styled-components";

import ThreeJs from "../threejs";

import { StyledArticle, ArticleButton, ArticleInfo, ArticleInfoP, ArticleInfoTitle } from "../styles/article";

const StyledThreeJs = styled(ThreeJs)`
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
`;

export default function GlArticle() {
    return (
        <StyledArticle>
            <ArticleInfo>
                <ArticleInfoTitle>GARAGE LEGENDS</ArticleInfoTitle>
                <ArticleInfoP>Coming Soon</ArticleInfoP>
                <ArticleButton>SIGN UP</ArticleButton>
            </ArticleInfo>
            <StyledThreeJs />
        </StyledArticle>
    );
};;;