import React from 'react';

import styled from "styled-components";

import colors from "../styles/styles";

const Nav = styled.nav`
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 75px;
    justify-content: center;
    align-items: center;
    background-color: ${colors.primary};
`;

const NavUl = styled.ul`
    display: flex;
    flex-direction: row;
    width: 80%;
    justify-content: center;
    margin:auto;
`;

const UlLi = styled.li`
    margin: 0 10px;
    list-style: none;
    padding: 1em;
`;

const A = styled.a`
    color: ${colors.textPrimary};
    font-weight: lighter;
    font-size: 1.35rem;
    text-decoration: none;
    padding: 10px;
`;

export default function NavBar() {
    return (
        <Nav id="nav">
            <NavUl>
                <UlLi><A href="#about">ABOUT</A></UlLi>
                <UlLi><A href="#home">HOME</A></UlLi>
                {/* <UlLi><A href="#gl">GARAGE LEGENDS</A></UlLi> */}
            </NavUl>
        </Nav>
    );
}