import colors from "./styles";
import styled from "styled-components";

interface IStyledArticle {
    bgcolor?: string;
    color?: string;
}

export const StyledArticle = styled.div<IStyledArticle>`
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 40vw;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    padding: 5em;

    margin: auto;
    overflow: hidden;

    color: ${props => props.color || `${colors.textPrimary}`};
    background-color: ${props => props.color || `${colors.primary}`};
`;

export const ArticleInfo = styled.div`
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    pointer-events: none;
    z-index: 2;

    gap: 5vw;
`;

export const ArticleInfoTitle = styled.h1`
    font-family: 'Inter', sans-serif;
    font-size: 3rem;
    font-weight: 600;
    color: ${colors.primary};
    text-shadow: 0 0 8px ${colors.secondary};
    word-spacing: 10px;
    letter-spacing: 18px;
`;

export const ArticleInfoP = styled.p`
    font-size: 1.25rem;
    font-weight: lighter;
    background-clip: text;
    mix-blend-mode: difference;
    filter: invert(1);
`;

export const ArticleButton = styled.button`
    width: 16vw;
    height: 50px;

    border-radius: 3px;
    border: none;

    font-size: 1.25rem;
    font-weight: lighter;

    color: ${colors.textPrimary};
    background-color: ${colors.primary};
    box-shadow: 0 0 8px ${colors.textPrimary};

    pointer-events: all;

    transition: all 0.2s ease-in-out;

    &:hover {
        color: ${colors.primary};
        background-color: ${colors.textPrimary};
        cursor: pointer;
    }

    &:focus {
        outline: none;
    }
`;