export function easeOutSine(x: number): number {
    return Math.sin((x * Math.PI) / 2);
}

export const delay = (ms: number) => new Promise(res => setTimeout(res, ms));

const SPEED_MULTIPLIER = 1.35;

export const customScrollTo = (to: number, duration: number, easingFunction: CallableFunction) => {
    const start = window.scrollY || window.scrollY;

    const time = Date.now();
    let timeElapsed = 0;

    const speed = (to - start) / duration;

    (function move() {
        if (timeElapsed > duration) return;

        timeElapsed = Date.now() - time;

        // Get the displacement of "y"
        const dy = (speed * SPEED_MULTIPLIER) * timeElapsed;
        let y = start + dy;

        // Map "y" into a range from 0 to 1
        let _y = (y - start) / (to - start);
        // Fit "_y" into a curve given by "easingFunction"
        _y = easingFunction(_y);
        // Expand "_y" into the original range
        y = start + (to - start) * _y;

        window.scrollTo(0, y);
        window.requestAnimationFrame(move);
    })();
};