import React from "react";
import { StyledArticle, ArticleButton, ArticleInfo, ArticleInfoP, ArticleInfoTitle } from "../styles/article";

import colors from "../styles/styles";

export default function TwoArticle() {

    return (
        <StyledArticle color={colors.secondary}>
            <ArticleInfo>
                <ArticleInfoTitle>ABOUT ME</ArticleInfoTitle>
                <ArticleButton>Read</ArticleButton>
            </ArticleInfo>
        </StyledArticle>
    );
}